  --font-primary: 'Spectral', serif;
  --font-secondary: 'EB Garamond', serif;

  --white: rgb(255, 255, 255);
  --bg-1: rgb(239, 208, 180);
  --bg-2: rgb(238, 198, 171);
  /* save rgb values separately to use in transparent rgba colours */
  --light-1-rgb: 255, 203, 155;
  --light-1: rgb(var(--light-1-rgb));
  --light-2: rgb(173, 142, 112);
  --dark-1: rgb(101, 77, 64);
  --dark-2-rgb: 90, 63, 46;
  --dark-2: rgb(var(--dark-2-rgb));
}

body {
  font-family: var(--font-primary);
  background: no-repeat top / cover fixed url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

h1, h2,
.motto {
  font-family: var(--font-secondary);
}

h1, h2,
.tab-btn {
  text-transform: uppercase;
  text-align: center;
}

h1,
.motto {
  color: var(--white);
  text-shadow: 0 0 20px rgba(var(--light-1-rgb), 0.8);
}

h1 {
  font-weight: 500;
  font-size: 4rem;
  letter-spacing: 70px;
}

nav {
  padding-top: 10px;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
}
.tab-btn {
  font-weight: 700;
  font-size: 1.2rem;
  color: var(--light-1);
  width: 100%;
  border-bottom: 1px solid transparent;

  &:hover,
  &:focus,
  &.selected {
    border-bottom-color: var(--light-2);
  }
}

.container {
  max-width: 1200px;
  margin: 40px auto 80px;
  padding: 40px 60px 60px;
  &.light {
    background: linear-gradient(var(--bg-1), var(--bg-2));
    color: var(--dark-1);
    box-shadow: 0 10px 20px rgba(var(--dark-2-rgb), 0.8);
  }
}

h2 {
  font-size: 1.8rem;
  letter-spacing: 10px;
  border-bottom: 1px solid var(--light-2);
  padding-bottom: 15px;
  margin-bottom: 50px;
}

h2, h3 {
  font-style: italic;
  color: var(--dark-2);
